@import './react-theme-css-variable/create-theme-color-hsl.scss';

:root {
  @include create-theme-color-complimentary(--background-color, #708090);
  @include create-theme-color(--background-contrast-color, #eee);
  @include create-theme-color(--primary-color, #ff6347);
}

%theme-presenter-container {
  background-color: #708090;
  padding: 1rem;
  border-radius: 6px;
  margin: 1rem;
}

body {
  background-color: var(--background-color);
  color: var(--background-contrast-color);
}

.App {
  font-family: sans-serif;
  text-align: center;

  h1 {
    color: var(--primary-color);

    &:hover {
      color: var(--primary-color-hover);
      cursor: pointer;
    }
  }

  button {
    padding: 10px;
    border: 1px solid;
    border-radius: 6px;
  }

  [data-theme='complimentary-theme'] {
    @include create-theme-color-complimentary(--primary-color, #00ffff);

    @extend %theme-presenter-container;

    h1 {
      color: var(--primary-color);

      &:hover {
        color: var(--primary-color-hover);
        cursor: pointer;
      }
    }

    h2 {
      color: var(--primary-color-complimentary);
    }
  }

  [data-theme='triadic-theme'] {
    @include create-theme-color-triadic(--primary-color, #00ffff);

    @extend %theme-presenter-container;

    h1 {
      color: var(--primary-color);

      &:hover {
        color: var(--primary-color-hover);
        cursor: pointer;
      }
    }

    h2 {
      color: var(--primary-color-triadic-first-hue);
    }

    h3 {
      color: var(--primary-color-triadic-second-hue);
    }
  }
}
