@mixin create-theme-color-complimentary($color-name, $color-value, $hover: 0.5) {
  @include create-theme-color($color-name, $color-value, $hover);

  $complimentary-factor: 180;

  #{$color-name}-complimentary: hsl(
    calc(var(#{$color-name + '-h'}) + $complimentary-factor),
    var(#{$color-name + '-s'}),
    calc(var(#{$color-name + '-l'}))
  );
}

@mixin create-theme-color-triadic($color-name, $color-value, $hover: 0.5) {
  @include create-theme-color($color-name, $color-value, $hover);

  $triadic-factor: 120;

  #{$color-name}-triadic-first-hue: hsl(
    calc(var(#{$color-name + '-h'}) + $triadic-factor),
    var(#{$color-name + '-s'}),
    calc(var(#{$color-name + '-l'}))
  );
  #{$color-name}-triadic-second-hue: hsl(
    calc(var(#{$color-name + '-h'}) + $triadic-factor * 2),
    var(#{$color-name + '-s'}),
    calc(var(#{$color-name + '-l'}))
  );
}

@mixin create-theme-color($color-name, $color-value, $hover: 0.5) {
  #{$color-name}-h: hue($color-value);
  #{$color-name}-s: saturation($color-value);
  #{$color-name}-l: lightness($color-value);
  #{$color-name}: hsl(var(#{$color-name + '-h'}), var(#{$color-name + '-s'}), var(#{$color-name + '-l'}));
  #{$color-name}-hover: hsla(
    var(#{$color-name + '-h'}),
    var(#{$color-name + '-s'}),
    var(#{$color-name + '-l'}),
    $hover
  );
}

@function change-alpha($color-name, $opacity) {
  @return hsla(var(#{$color-name + '-h'}), var(#{$color-name + '-s'}), var(#{$color-name + '-l'}), $opacity);
}

@function change-lightness($color-name, $lightness) {
  @return hsl(var(#{$color-name + '-h'}), var(#{$color-name + '-s'}), calc(var(#{$color-name + '-l'}) + #{$lightness}));
}
